@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap);
body{background-color:#0d2735}#header{height:130px;transition:all 0.5s;z-index:5;transition:all 0.5s;background:#1e4356;position:absolute;width:100%}#header.header-transparent{background:none}#header.header-scrolled{background:rgba(30,67,86,0.8);height:60px}#header .logo{margin:0 auto}#header .logo .text-light{box-shadow:0 0 3px #ffffff6b;display:flex;width:240px;text-align:center;height:50px;align-items:center;justify-content:center;border-radius:5px}#header .logo h1{font-size:32px;padding:4px 0;font-weight:400;letter-spacing:3px;text-transform:uppercase}#header .logo h1 a,#header .logo h1 a:hover{color:#fff;text-decoration:none}#header .logo img{padding:0;margin:0;max-height:40px}#main{margin-top:80px}#hero{width:100%;height:90vh;overflow:hidden;position:absolute;z-index:0;background-color:#1e4356}#hero::after{content:'';position:absolute;left:50%;top:0;width:130%;height:95%;background:linear-gradient(to right, rgba(30,67,86,0.8), rgba(30,85,52,0.6)),url(/cvcreator/static/media/hero-bg.cbd9749c.jpg) center top no-repeat;z-index:0;border-radius:0 0 50% 50%;-webkit-transform:translateX(-50%) rotate(0deg);transform:translateX(-50%) rotate(0deg)}#hero::before{content:'';position:absolute;left:50%;top:0;width:130%;height:96%;background:#68A4C4;opacity:0.9;z-index:0;border-radius:0 0 50% 50%;-webkit-transform:translateX(-50%) translateY(18px) rotate(2deg);transform:translateX(-50%) translateY(18px) rotate(2deg)}#hero h2{color:#fff;margin-bottom:30px;font-size:48px;font-weight:700}#hero p{width:80%;-webkit-animation-delay:0.4s;animation-delay:0.4s;margin:0 auto 30px auto;color:#fff}#hero .btn-get-started{font-family:"Roboto",sans-serif;font-weight:500;font-size:14px;letter-spacing:1px;display:inline-block;padding:12px 32px;border-radius:50px;transition:0.5s;line-height:1;margin:10px;color:#fff;-webkit-animation-delay:0.8s;animation-delay:0.8s;border:2px solid #68A4C4}#hero .btn-get-started:hover{background:#68A4C4;color:#fff;text-decoration:none}@media (min-width: 1024px){#hero p{width:60%}#hero .carousel-control-prev,#hero .carousel-control-next{width:5%}}@media (max-width: 768px){#hero::after{width:180%;height:95%;border-radius:0 0 50% 50%;-webkit-transform:translateX(-50%) rotate(0deg);transform:translateX(-50%) rotate(0deg)}#hero::before{top:0;width:180%;height:94%;border-radius:0 0 50% 50%;-webkit-transform:translateX(-50%) translateY(20px) rotate(4deg);transform:translateX(-50%) translateY(20px) rotate(4deg)}}@media (max-width: 575px){#hero h2{font-size:30px}#hero::after{left:40%;top:0;width:200%;height:95%;border-radius:0 0 50% 50%;-webkit-transform:translateX(-50%) rotate(0deg);transform:translateX(-50%) rotate(0deg)}#hero::before{left:50%;top:0;width:200%;height:94%;border-radius:0 0 50% 50%;-webkit-transform:translateX(-50%) translateY(20px) rotate(4deg);transform:translateX(-50%) translateY(20px) rotate(4deg)}}#hero-no-slider{width:100%;height:100vh;overflow:hidden;position:relative;text-align:center}#hero-no-slider::before{content:'';position:absolute;left:0;top:0;right:0;bottom:0;background:linear-gradient(to right, rgba(30,67,86,0.8), rgba(30,67,86,0.6)),url(/cvcreator/static/media/hero-bg.cbd9749c.jpg) center top no-repeat}#hero-no-slider h2{color:#fff;margin-bottom:15px;font-size:48px;font-weight:700}#hero-no-slider p{color:#fff}#hero-no-slider .btn-get-started{font-family:"Roboto",sans-serif;font-weight:500;font-size:14px;letter-spacing:1px;display:inline-block;padding:12px 32px;border-radius:50px;transition:0.5s;line-height:1;margin:10px;color:#fff;-webkit-animation-delay:0.8s;animation-delay:0.8s;border:2px solid #68A4C4}#hero-no-slider .btn-get-started:hover{background:#68A4C4;color:#fff;text-decoration:none}@media (max-width: 575px){#hero-no-slider h2{font-size:30px}}.edit-icon-enter{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9)}.edit-icon-enter-active{opacity:1;-webkit-transform:translateX(0);transform:translateX(0);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}.edit-icon-exit{opacity:1}.edit-icon-exit-active{opacity:0;-webkit-transform:scale(0.9);transform:scale(0.9);transition:opacity 300ms, -webkit-transform 300ms;transition:opacity 300ms, transform 300ms;transition:opacity 300ms, transform 300ms, -webkit-transform 300ms}.form-modal{position:fixed;z-index:15;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4)}.form-container{background-color:#fefefee5;margin:15% auto;padding:30px;border:1px solid #888;width:500px;box-shadow:0 0 30px rgba(214,215,216,0.2);position:relative}.form-container input,.form-container .text-area{border-radius:2px}.form-container .text-area{height:160px}.form-container fieldset{margin-bottom:15px;padding:10px;box-shadow:0 0 5px #a2cce3;text-align:right}.form-container button{background:#68A4C4;border:0;padding:10px 24px;color:#fff;transition:0.4s}.form-container button:hover{background:#a2cce3}.form-container .create-field-group{width:50%;height:70px;color:#68A4C4;box-shadow:0 0 5px #a2cce3;border:0;display:flex;align-items:center;justify-content:center;margin:0 auto;background-color:#fff}.form-container .create-field-group i.add-icon{font-size:2em;margin:5px}.form-container .create-field-group:hover{color:#a2cce3}.form-container i.close-input-group{color:#68A4C4;font-size:2em;margin:5px;position:relative;top:-5px}.form-container i.close-input-group:hover{color:#a2cce3}.form-container i.close-form{color:#444;font-size:2em;margin:5px;position:absolute;top:0;right:0;width:50px}.form-container i.close-form:hover{color:#666}footer{background:#0b212d;padding:0 0 30px 0;color:#b8b8bd;font-size:14px}footer .footer-top{background:#1e4356;border-top:1px solid #17455e;border-bottom:1px solid #123649;padding:60px 0 30px 0;height:90px}footer .copyright{text-align:center;padding-top:30px}

